@import "sass/index";

$imgSize: 160px;

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.qrCode {
  margin: spacing(1) 0 spacing(4.5);
  min-width: $imgSize;
  min-height: $imgSize;
  width: $imgSize;
  height: $imgSize;
  transition: opacity 0.4s;
  background-color: white;
  border-radius: spacing(1);
  opacity: 1;
}
.qrDisable {
  opacity: 0;
}
.reminderBlock {
  margin: spacing(3) 0 0;
}
.reminderTitle {
  font-size: 12px;
  font-weight: bold;
  color: rgba(white, 0.5);
  letter-spacing: 0.86px;
  margin-bottom: 8px !important;
}
.reminderText {
  font-size: 13px;
  color: rgba(white, 0.5);
  font-family: Helvetica;
}
.boldText {
  font-weight: bold;
}
.inputWrapper {
  position: relative;
  width: 100%;
}
.inputInnerWrapper {
  position: relative;
  width: 100%;
}
.inputCopyButton {
  position: absolute;
  right: 2px;
  bottom: -3px;
}
.input {
}
.loader {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;

  & circle {
    color: white;
  }
}
.button {
  padding: 4px 0;
  width: 100%;
}

.reminderBlockContainer {
  display: flex;
}
.leftBlock {
  flex: 1;
}
.rightBlock {
  max-width: 130px;
  margin-left: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.rightBlockWrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  min-width: 130px;
  min-height: 128px;
  padding: 16px;

  background: rgba(white, 0.1);
  border-radius: spacing(1);
}
.facePayDepositTitle {
  font-size: 10px;
  font-weight: bold;
  color: rgba(white, 0.5);
  letter-spacing: 0.9px;
  margin-bottom: 4px !important;
  text-align: center;
}
.logo {
  margin-bottom: 12px;
}
